.player {
    display: flex;
    min-height: 20vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .time-control {
        width: 50%;
        display: flex;
        align-items: center;
        input {
            width: 100%;
            -webkit-appearance: none;
            background: transparent;
            cursor: pointer;

            &[type="range"]:focus {
                outline: none;
            }
            &[type="range"]::-webkit-slider-thumb {
                -webkit-appearance: none;
                height: 16px;
                width: 16px;
            }
            &[type="range"]::-moz-range-thumb {
                -webkit-appearance: none;
                background: transparent;
                border: none;
            }
        }
        p {
            padding: 1rem;
        }
    }

    .play-control {
        width: 30%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        svg {
            cursor: pointer;
        }
    }
}

.track {
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}
.animate-track {
    background-color: rgb(196, 196, 196);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}

@media screen and (max-width: 768px) {
    .player .time-control {
        width: 90%;
    }
    .player .play-control {
        width: 60%;
    }
    
}